import React from "react";
import "./Detail.css";

class Detail extends React.Component {
  componentDidMount() {
    const { location, history } = this.props;

    if (location.state === undefined) {
      history.push("/");
    }
  }
  render() {
    console.log(this.props);
    const { location } = this.props;
    if (location.state) {
      return ren(location);
    } else {
      return null;
    }
  }
}

function ren(location) {
  return (
    <div className="details">
      <img src={location.state.poster} className="detail__image" />
      <div className="detail__text">
        <span className="detail__title">{location.state.title}</span>
        <span className="detail__year">{location.state.year}</span>
        <ul className="detail__genres">
          <li className="genres">{location.state.genres[0]}</li>
          <li className="genres">{location.state.genres[1]}</li>
          <li className="genres">{location.state.genres[2]}</li>
        </ul>

        <span className="detail__summary">{location.state.summary}</span>
      </div>
    </div>
  );
}

export default Detail;
